/* 03. MAIN NAVIGATION MENU */
/***********************************************************************************************/

.navbar-header{
    a.logo {
        margin-top: 16px;
        display: inline-block;

        img{
            display: block;
        }
    }
}

.navbar-default {
    border: none;
    background: transparent;
    margin-bottom: 0px;
    padding: 0 !important;


    .navbar-nav {
        margin-top: 10px;
    }
    .navbar-nav > li {
        margin-left: 20px;
    }
    .navbar-nav > li > a {
        color: #FFF;
        font-size: 14px;
        font-weight: 500;
        text-transform: uppercase;
        padding-bottom: 22px;
        line-height: 32px;
    }
    .navbar-nav > li > a:hover {
        color: #EEEEEE;
    }
    .navbar-nav li.drop {
        position: relative;
        a{

            &:after{
                content: "\f107";
                font-family: fontawesome;
                position: absolute;
                right: -3px;
                top: 15px;
                font-weight: normal;
            }

        }
        
    }
    .navbar-nav li ul.drop-down {
        margin: 0;
        padding: 0;
        position: absolute;
        top: 69px;
        left: 0;
        width: 160px;
        visibility: hidden;
        opacity: 0;
        z-index: 3;
        text-align: left;
        .transition(all, 0.3s);
    }
    .navbar-nav li ul.drop-down li {
        list-style: none;
        display: block;
        margin: 0;
    }
    .navbar-nav li ul.drop-down li ul.drop-down.level3 {
        -webkit-transform: rotateX(0deg);
        -moz-transform: rotateX(0deg);
        -ms-transform: rotateX(0deg);
        -o-transform: rotateX(0deg);
        transform: rotateX(0deg);
        -webkit-transform: rotateX(-90deg);
        -moz-transform: rotateX(-90deg);
        -ms-transform: rotateX(-90deg);
        -o-transform: rotateX(-90deg);
        transform: rotateX(-90deg);
        top: 0px;
        left: 100%;
        border-bottom: none;
        a{
            
            &:after{
                content: '';
            }
            
        }
    }
    .navbar-nav li ul.drop-down li a {
        display: inline-block;
        text-decoration: none;
        display: block;
        color: @light-text-color;
        font-size: 14px;
        padding: 12px 20px;
        text-transform: uppercase;
        font-weight: 500;
        background: rgba(0,0,0,0.7);
        margin: 0;
        border: none;
        
         &:after{
            content: '';
            font-family: FontAwesome;
            position: absolute;
            right: 12px;
        }
    }
    
    .navbar-nav li ul.drop-down li a:hover {

        background: rgba(0,0,0,0.8);
        color: @light-text-color;
    }
    
    .navbar-nav li ul.drop-down li.drop a {
        display: inline-block;
        text-decoration: none;
        display: block;
        color: @light-text-color;
        font-size: 14px;
        padding: 12px 20px;
        text-transform: uppercase;
        font-weight: 500;
        margin: 0;
        border: none;
        
         &:after{
            content: '\f105';
            font-family: FontAwesome;
            position: absolute;
            right: 12px;
        }
    }
    
    .navbar-nav li ul.drop-down li:hover ul.drop-down.level3 {
        -webkit-transform: rotateX(0deg);
        -moz-transform: rotateX(0deg);
        -ms-transform: rotateX(0deg);
        -o-transform: rotateX(0deg);
