// NEWS BLOCK

.section-news-block{

    .news-wrapper{

        background: @pure-white-bg;
        border: 1px solid @light-border-color;

        .news-img{

            display: block;
            position: relative;
            overflow: hidden;

            img{
                margin-bottom: 0px;
                width:100%;
                display: block;
            }

            .date-meta{
                position: absolute;
                left: 0;
                bottom: 0;
                display: inline-block;
                width: 60px;
                background: @primary-color;
                font-size: 30px;
                width: 110px;
                text-align: center;
                color: @light-text-color;
                padding: 6px 0;
                font-weight: 600;

                span{
                    font-size: 16px;
                    display: block;
                }
            }

        }

        .news-meta{
            list-style: none;
            overflow: hidden;

            li{
                display: block;
            }
            margin: 6px 0;
        }

        .news-button{
            border-radius: 0px;
            border: 0px;
            transition: all 0.4s ease 0s;
            background: @primary-color;
            font-size: 14px;
            color: @light-text-color;

            &:hover{
                background:#FFFFFF;
                border: 0px;
                color: @primary-color;

            }

        }

        .news-content{

            padding: 0px 12px 24px 12px;

            .news-text{

                h5{

                    a{
                        font-family: @heading-font-alt;
                        margin-top: 32px;
                        color: #444444;
                        font-size: 18px;
                        line-height: 32px;
                        display: block;
                        text-transform: capitalize;
                        font-weight: 400;
                    }
                }

                p{
                    font-size: 16px; 
                    font-weight: normal; 
                }

            }


        }

        // Hover Elements.

        &:hover{

            .news-img{

                &:before{
                    width: 100%;
                    background: rgba(0,0,0,0.5);
                }
            }

            .news-button{
                background: @primary-color;
                color: #FFFFFF;

                .fa{
                    color: #FFFFFF;

                }

            }
        }

    }


}

