//CONTACT SECTION.

.section-contact-block{

    position: relative;
    overflow: hidden;

    .content-block{
        padding:  0 48px 0 0;
    }

    .contact-title{
        font-size: 24px;
        border-bottom: 3px solid #eeeeee;
        color: #5c5c5c;
        padding-bottom: 15px;
        position: relative;

        &:after {
            bottom: -3px;
            content: "";
