            line-height: 42px;
            height: 42px;
            margin-bottom: 24px;
            padding-left: 2px;
            .round(0);
            .drop-shadow(0,0,0,0);
            .inner-shadow(0,0,0,0);
            .transition( 0.3s);
            &:focus{

                .drop-shadow(0,0,0,0);
                .inner-shadow(0,0,0,0);
                border: 0px;
                border-bottom: 1px solid lighten(@dark-border-color,50%);

            }

        }


        textarea{
            border: 0px;
            border-bottom: 1px solid @border-color;
            padding-left: 2px;
            margin-bottom: 24px;
            .round(0);
            .drop-shadow(0,0,0,0);
            .inner-shadow(0,0,0,0);
            .transition( 0.3s);
            &:focus{

